import React, {Component} from 'react';
import {withFirebase} from '../firebase/context';

class Summary extends Component {
    state = {
        donations: [],
    };

    componentDidMount() {
        this.getAllData();
    }

    getAllData = () => {


        this.props.firebase.getDonation()
            .get()
            .then((donations => {
                const donationList = [];

                donations.forEach(doc => {
                    const donation = doc.data();
                    donation.id = doc.id;

                    donationList.push(donation)

                });

                console.log(donationList);
                this.setState({
                    donations: donationList,
                })
            }))
    };

    render() {

        return (
            <table className='donationTable'>
                <thead>
                    <tr>
                        <th>No.</th>
                        <th>Email</th>
                        <th>Donacje</th>
                        <th>Adres</th>
                        <th>Telefon</th>
                        <th>Informacje dla kuriera</th>
                        <th>Status</th>
                    </tr>
                </thead>
                <tbody>
                        {this.state.donations.map( (donation, index)  => {
                                return(
                                    <tr>
                                        <td>{index + 1}</td>
                                        <td>{donation.email}</td>
                                        <td>Worki: {donation.donations[0].howMany},<br/>
                                            {donation.donations[0].what},<br/>
                                            {donation.donations[0].where},<br/>
                                            {donation.donations[0].who}
                                        </td>
                                        <td>{donation.donations[0].address.street},<br/>
                                            {donation.donations[0].address.city},<br/>
                                            {donation.donations[0].address.zipcode}<br/>
                                        </td>
                                        <td>
                                            {donation.donations[0].address.phone}
                                        </td>
                                        <td>
                                            {donation.donations[0].address.date},<br/>
                                            {donation.donations[0].address.time},<br/>
                                            {donation.donations[0].address.notes}
                                        </td>
                                        <td>Wysłane/W trakcie/Zakończone</td>
                                    </tr>
                                )
                            }
                        )}
                </tbody>
            </table>
        )
    }
}

export default withFirebase(Summary);